import { createContext } from "react";

export const ProjectTodoContext = createContext();

export const ProjectContext = ({ children }) => {
  const [input, setInput] = useState("");
  const [spaceData, setSpaceData] = useState(null);
  const [showAssign, setShowAssign] = useState(null);
  const [assignPriority, setAssignPriority] = useState(null);
  const [showMenu, setShowMenu] = useState(null);
  const [showStatusOption, setShowStatusOption] = useState(null);
  const [editingTaskId, setEditingTaskId] = useState(null);
  const [editingTaskValue, setEditingTaskValue] = useState("");

  const addProjectTask = (proj) => {
    if (!input.trim()) return;

    const newTask = {
      id: Date.now(),
      task: input.trim(),
      assignTo: proj.assignee ? [proj.assignee] : [],
      dueDate: proj.dueDate || "",
      priority: proj.priority || "Low",
      status: "Pending",
    };

    const updatedProjects = projectDetails.map((p) => {
      if (p.id === proj.id) {
        return { ...p, tasks: [...(p.tasks || []), newTask] };
      }
      return p;
    });

    setProjectDetails(updatedProjects);
    setInput("");
  };

  const updateTaskStatus = (projId, taskId, newStatus) => {
    const updatedProjects = projectDetails.map((project) => {
      if (project.id === projId) {
        return {
          ...project,
          tasks: project.tasks.map((t) =>
            t.id === taskId ? { ...t, status: newStatus } : t
          ),
        };
      }
      return project;
    });

    setProjectDetails(updatedProjects);
    setShowStatusOption(null);
  };

  const editTask = (projId, taskId, newValue) => {
    if (!newValue.trim()) return;

    const updatedProjects = projectDetails.map((project) => {
      if (project.id === projId) {
        return {
          ...project,
          tasks: project.tasks.map((t) =>
            t.id === taskId ? { ...t, task: newValue } : t
          ),
        };
      }
      return project;
    });

    setProjectDetails(updatedProjects);
    setEditingTaskId(null);
    setEditingTaskValue("");
    setShowMenu(null);
  };

  const assignUser = (value, taskId) => {
    const updatedProjects = projectDetails.map((project) => ({
      ...project,
      tasks: project.tasks.map((task) => {
        if (task.id === taskId) {
          const alreadyAssigned = task.assignTo.includes(value);
          return {
            ...task,
            assignTo: alreadyAssigned
              ? task.assignTo
              : [...task.assignTo, value],
          };
        }
        return task;
      }),
    }));

    setProjectDetails(updatedProjects);
    setShowAssign(null);
  };

  const updateDueDate = (projId, taskId, newDate) => {
    const updatedProjects = projectDetails.map((project) => {
      if (project.id === projId) {
        return {
          ...project,
          tasks: project.tasks.map((t) =>
            t.id === taskId ? { ...t, dueDate: newDate } : t
          ),
        };
      }
      return project;
    });

    setProjectDetails(updatedProjects);
  };

  const handlePriority = (value, taskId) => {
    const updatedProjects = projectDetails.map((project) => ({
      ...project,
      tasks: project.tasks.map((t) =>
        t.id === taskId ? { ...t, priority: value } : t
      ),
    }));

    setProjectDetails(updatedProjects);
    setAssignPriority(null);
  };

  const handleDelete = (taskId) => {
    const updatedProjects = projectDetails.map((project) => ({
      ...project,
      tasks: project.tasks.filter((cur) => cur.id !== taskId),
    }));

    setProjectDetails(updatedProjects);
    setShowMenu(null);
  };

  const users = ["Dhannu", "Rohit", "Amit", "Rupak", "Himanshu"];
  const priority = ["High", "Medium", "Low"];

  return (
    <ProjectTodoContext.Provider
      value={{
        addProjectTask,
        updateTaskStatus,
        editTask,
        assignUser,
        updateDueDate,
        handlePriority,
        handleDelete,
        users,
        priority,
      }}
    >
      {children}
    </ProjectTodoContext.Provider>
  );
};
