"use client";
import React, { useContext, useEffect, useState } from "react";
import { AiOutlineThunderbolt } from "react-icons/ai";
import { CiViewList } from "react-icons/ci";
import { FaFlipboard } from "react-icons/fa";
import { FcCalendar } from "react-icons/fc";
import { GiArtificialHive } from "react-icons/gi";
import {
  MdDeleteOutline,
  MdDownloadDone,
  MdOutlineModeEdit,
  MdSupportAgent,
} from "react-icons/md";
import { BiDotsVerticalRounded } from "react-icons/bi";
import { IoIosFlag } from "react-icons/io";
import { IoMdPeople } from "react-icons/io";
import { ProjectContext } from "../context/ProjectTodoContext";

export default function ProjectTodo({ projectDetails, setProjectDetails, ID }) {
  const {
    addProjectTask,
    updateTaskStatus,
    editTask,
    assignUser,
    updateDueDate,
    handlePriority,
    handleDelete,
    users,
    priority,
  } = useContext(ProjectContext);

  
  return (
    <div className="w-full h-full bg-zinc-950 p-4 rounded-2xl">
      <div className="w-full flex justify-between">
        <h1 className="text-xl font-medium">
          {spaceData?.spaceList || "dhannu workspace"}
        </h1>
        <div className="flex gap-6 text-[15px]">
          <div className="flex items-center gap-2">
            <MdSupportAgent size={17} /> Agents
          </div>
          <div className="flex items-center gap-2">
            <AiOutlineThunderbolt size={17} /> Automate
          </div>
          <div className="flex items-center gap-2">
            <MdSupportAgent size={17} /> Ask AI
          </div>
          <div className="flex items-center gap-2">
            <GiArtificialHive size={17} /> Share
          </div>
        </div>
      </div>

      <div className="flex items-center gap-4 border-b border-gray-800 py-2 text-sm text-gray-300">
        <div className="flex items-center gap-1 cursor-pointer hover:text-white">
          <CiViewList /> <span>List</span>
        </div>
        <div className="flex items-center gap-1 cursor-pointer hover:text-white">
          <FaFlipboard /> <span>Board</span>
        </div>
        <div className="flex items-center gap-1 cursor-pointer hover:text-white">
          <FcCalendar /> <span>Calendar</span>
        </div>
        <div className="flex items-center gap-1 border-l border-gray-700 pl-3 cursor-pointer hover:text-white">
          <span>+</span>
          <span>View</span>
        </div>
      </div>

      <div className="w-full py-5">
        {projectDetails.map((proj) => (
          <div
            key={proj.id}
            className="w-full flex flex-col mb-6 border-zinc-700 pb-4"
          >
            <div className="w-full flex justify-between items-center mb-2">
              <h1 className="text-xl font-semibold text-pink-600">
                {proj.name}
              </h1>
              <div className="flex text-sm space-x-3.5">
                <div className="p-2 w-[120px] font-semibold">Assignee</div>
                <div className="p-2 w-[120px] font-semibold">Due date</div>
                <div className="p-2 w-[120px] font-semibold">Priority</div>
                <div className="p-2 w-[120px] font-semibold">Menu</div>
              </div>
            </div>

            {/* Task list */}
            {proj.tasks &&
              proj.tasks.map((task, index) => (
                <div
                  key={task.id}
                  className="w-full border-b border-t border-zinc-800 mt-1 text-zinc-400 items-center px-2 flex justify-between text-sm mb-1"
                >
                  <div className="text-base flex items-center justify-start gap-2 font-medium text-zinc-300 w-1/3">
                    {/* Status checkbox */}
                    <div
                      onClick={() =>
                        setShowStatusOption((prev) =>
                          prev === index ? null : index
                        )
                      }
                      className="relative flex items-center gap-2"
                    >
                      <input
                        type="checkbox"
                        checked={task.status === "Completed"}
                        readOnly
                        className={`w-4 h-4 rounded-full appearance-none border cursor-pointer
                        ${
                          task.status === "Progress"
                            ? "bg-purple-600 border-purple-600"
                            : ""
                        }
                        ${
                          task.status === "Completed"
                            ? "bg-green-600 border-green-600"
                            : ""
                        }
                        ${
                          task.status === "Pending"
                            ? "bg-transparent border-gray-400"
                            : ""
                        }`}
                      />
                      {showStatusOption === index && (
                        <div
                          onClick={(e) => e.stopPropagation()}
                          className="absolute left-2 top-6 bg-zinc-900 border border-zinc-700 rounded-md shadow-lg z-50"
                        >
                          <button
                            onClick={() =>
                              updateTaskStatus(proj.id, task.id, "Progress")
                            }
                            className="block px-4 py-2 text-sm text-white hover:bg-zinc-800 w-full"
                          >
                            Progress
                          </button>
                          <button
                            onClick={() =>
                              updateTaskStatus(proj.id, task.id, "Completed")
                            }
                            className="block px-4 py-2 text-sm text-white hover:bg-zinc-800 w-full"
                          >
                            Completed
                          </button>
                        </div>
                      )}
                    </div>

                    {editingTaskId === task.id ? (
                      <input
                        type="text"
                        value={editingTaskValue}
                        onChange={(e) => setEditingTaskValue(e.target.value)}
                        onKeyDown={(e) => {
                          if (e.key === "Enter")
                            editTask(proj.id, task.id, editingTaskValue);
                        }}
                        className="p-1 w-full rounded-md text-sm outline-none text-zinc-200"
                      />
                    ) : (
                      <span>{task.task}</span>
                    )}
                  </div>

                  <div className="flex">
                    {/* Assign user */}
                    <div
                      onClick={() =>
                        setShowAssign((prev) => (prev === index ? null : index))
                      }
                      className="p-2 hover:bg-zinc-800 rounded-md relative w-[120px] flex gap-1"
                    >
                      {task.assignTo?.length > 0 ? (
                        task.assignTo.map((name, i) => (
                          <span
                            key={i}
                            className="h-6 w-6 mt-0.5 flex justify-center items-center rounded-full text-white text-xs bg-purple-600"
                          >
                            {name.charAt(0).toUpperCase()}
                          </span>
                        ))
                      ) : (
                        <span className="hover:bg-zinc-800 rounded-md w-full h-full flex items-center px-2 py-1">
                          <IoMdPeople size={20} />
                        </span>
                      )}

                      {showAssign === index && (
                        <div
                          onClick={(e) => e.stopPropagation()}
                          className="absolute left-0 top-10 z-50 w-[150px] p-2 bg-black rounded-md shadow-lg"
                        >
                          {users.map((cur, idx) => (
                            <div
                              key={idx}
                              onClick={() => assignUser(cur, task.id)}
                              className="p-2 hover:bg-zinc-900 rounded-md cursor-pointer"
                            >
                              {cur}
                            </div>
                          ))}
                        </div>
                      )}
                    </div>

                    {/* Due Date */}
                    <div className="p-2 w-[135px] hover:bg-zinc-800 rounded-md mr-4 flex items-center justify-start">
                      <input
                        type="date"
                        onChange={(e) =>
                          updateDueDate(proj.id, task.id, e.target.value)
                        }
                        value={task.dueDate || ""}
                        className="bg-transparent rounded-md outline-none mr-2"
                      />
                    </div>

                    {/* Priority */}
                    <div
                      onClick={() =>
                        setAssignPriority((prev) =>
                          prev === index ? null : index
                        )
                      }
                      className="p-2 hover:bg-zinc-800 rounded-md flex items-center w-[120px] relative"
                    >
                      {task.priority === "High" ? (
                        <IoIosFlag size={20} className="text-red-600" />
                      ) : task.priority === "Medium" ? (
                        <IoIosFlag size={20} className="text-yellow-600" />
                      ) : (
                        <IoIosFlag size={20} className="text-white" />
                      )}

                      {assignPriority === index && (
                        <div
                          onClick={(e) => e.stopPropagation()}
                          className="p-2 w-[150px] left-0 top-10 bg-black z-50 absolute "
                        >
                          {priority.map((cur, idx) => (
                            <div
                              key={idx}
                              onClick={() => handlePriority(cur, task.id)}
                              className="p-2 hover:bg-zinc-900 rounded-md cursor-pointer"
                            >
                              {cur}
                            </div>
                          ))}
                        </div>
                      )}
                    </div>

                    {/* Menu */}
                    <div
                      onClick={() =>
                        setShowMenu((prev) => (prev === index ? null : index))
                      }
                      className="p-2 hover:bg-zinc-800 rounded-md relative w-[120px] flex items-center justify-start px-6"
                    >
                      <BiDotsVerticalRounded size={20} />
                      {showMenu === index && (
                        <div className="absolute left-6 top-10 bg-zinc-900 border border-zinc-700 rounded-md shadow-lg z-50 w-[100px]">
                          <button
                            onClick={() => {
                              setEditingTaskId(task.id);
                              setEditingTaskValue(task.task);
                              setShowMenu(null);
                            }}
                            className="block px-4 py-2 text-sm text-zinc-300 hover:bg-zinc-800 rounded-md w-full"
                          >
                            <span className="flex items-center gap-2">
                              <MdOutlineModeEdit size={15} />
                              <p className="text-[14px]">Edit</p>
                            </span>
                          </button>

                          {editingTaskId === task.id && (
                            <button
                              onClick={() =>
                                editTask(proj.id, task.id, editingTaskValue)
                              }
                              className="block px-4 py-2 text-sm text-green-200 hover:bg-zinc-800 rounded-md w-full"
                            >
                              <span className="flex items-center gap-2">
                                <MdDownloadDone size={16} />
                                <p className="text-[14px]">Save</p>
                              </span>
                            </button>
                          )}

                          <button
                            onClick={() => handleDelete(task.id)}
                            className="block px-4 py-2 text-sm text-red-200 rounded-md hover:bg-zinc-800 w-full"
                          >
                            <span className="flex items-center gap-2">
                              <MdDeleteOutline size={15} />
                              <p className="text-[14px]">Delete</p>
                            </span>
                          </button>
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              ))}

            {/* Add Task Input */}
            <div className="p-1 mt-2 flex border-t border-zinc-800 border-b w-full justify-between items-center">
              <div className="w-1/2">
                <input
                  type="text"
                  value={input}
                  placeholder="Add Task"
                  onChange={(e) => setInput(e.target.value)}
                  className="p-2 w-full text-sm rounded-md outline-none"
                />
              </div>
              <div>
                <button
                  onClick={() => addProjectTask(proj)}
                  className="bg-purple-600 p-2 text-xs rounded-md hover:bg-purple-700 text-white"
                >
                  Add Task
                </button>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
